.md2-dialog-open {
  overflow-y: hidden; }

.md2-dialog {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1050;
  background-color: rgba(33, 33, 33, 0.48);
  display: none;
  overflow-x: hidden;
  overflow-y: scroll;
  -webkit-overflow-scrolling: touch;
  outline: 0; }

.md2-dialog.open {
  display: block; }

.md2-dialog .md2-dialog-container {
  position: relative;
  width: auto;
  margin: 15px;
  background-color: #fff;
  background-clip: padding-box;
  border-radius: 0 0 4px 4px;
  outline: 0;
  box-shadow: 0 7px 8px -4px rgba(0, 0, 0, 0.2), 0 13px 19px 2px rgba(0, 0, 0, 0.14), 0 5px 24px 4px rgba(0, 0, 0, 0.12);
  transition: 300ms;
  transform: scale(0.1); }

.md2-dialog.open .md2-dialog-container {
  transform: scale(1); }

@media (min-width: 768px) {
  .md2-dialog .md2-dialog-container {
    width: 600px;
    margin: 30px auto; } }

.md2-dialog-header {
  background: #2196f3;
  color: #fff;
  font-size: 25px;
  line-height: 1.1;
  font-weight: 500;
  padding: 0 48px 0 16px;
  border-bottom: 1px solid #e5e5e5;
  word-wrap: break-word; }
  .md2-dialog-header .close {
    position: absolute;
    top: 21px;
    right: 16px;
    display: inline-block;
    width: 18px;
    height: 18px;
    overflow: hidden;
    -webkit-appearance: none;
    padding: 0;
    cursor: pointer;
    background: 0 0;
    border: 0;
    outline: 0;
    opacity: 0.8;
    font-size: 0;
    z-index: 1;
    min-width: initial;
    box-shadow: none;
    margin: 0; }
    .md2-dialog-header .close::before, .md2-dialog-header .close::after {
      content: '';
      position: absolute;
      top: 50%;
      left: 0;
      width: 100%;
      height: 2px;
      margin-top: -1px;
      background: #ccc;
      border-radius: 2px; }
    .md2-dialog-header .close::before {
      transform: rotate(45deg); }
    .md2-dialog-header .close::after {
      transform: rotate(-45deg); }
    .md2-dialog-header .close:hover {
      opacity: 1; }
  .md2-dialog-header md2-dialog-title,
  .md2-dialog-header .md2-dialog-title {
    display: block;
    margin: 0;
    padding: 16px 0;
    font-size: 25px;
    font-weight: 500; }
  .md2-dialog-header dialog-header {
    line-height: 33px; }

.md2-dialog-body {
  position: relative;
  padding: 16px; }

.md2-dialog-footer,
md2-dialog-footer {
  display: block;
  padding: 16px;
  text-align: right;
  border-top: 1px solid rgba(0, 0, 0, 0.12); }
